import "./App.css";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import Main from "./Main";
import NaverMain from "./components/naver/pages/Main";
import NaverBook from "./components/naver/pages/Book";
import NaverMovie from "./components/naver/pages/Movie";
import NaverBookDetail from "./components/naver/pages/BookDetail";
import Naver from "./components/naver/organisms/Naver";

import SearchVideo from "./components/Youtube/templates/SearchVideo";
import Videolist from "./components/Youtube/modules/Videolist";
import YoutubeMain from "./components/Youtube/templates/Main";
import Watchvedio from "./components/Youtube/templates/Watchvedio";
import Index from "./components/Homepage/templates/Index";

import Page from "./components/Homepage/templates/Page";
import Companey from "./components/Homepage/modules/Companey";
import Location from "./components/Homepage/modules/Location";
import Video from "./components/Homepage/modules/Video";
import Group from "./components/Homepage/modules/Group";
import Personal from "./components/Homepage/modules/Personal";
import Request from "./components/Homepage/modules/Request";
import GroupLesson from "./components/Homepage/modules/GroupLesson";
import PersonalLesson from "./components/Homepage/modules/PersonalLesson";
import Seminar from "./components/Homepage/modules/Seminar";
import Interview from "./components/Homepage/modules/Interview";
import Review from "./components/Homepage/modules/Review";
import Todo from "./components/Todo/Todo";
import Cocoa from "./components/CocoaClone/Cocoa";

function App() {
  return (
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <Routes>
        <Route path="/" element={<Main />} />

        <Route path="/Todo" element={<Todo />} />
        <Route path="/cocoa" element={<Cocoa />} />

        <Route path="/naver" element={<Naver />}>
          <Route path="/naver" element={<NaverMain />} />
          <Route path="book" element={<NaverBook />} />
          <Route path="movie" element={<NaverMovie />} />
          <Route path="book/:isbn" element={<NaverBookDetail />} />
        </Route>

        <Route path="/youtube" element={<YoutubeMain />}>
          <Route index element={<Videolist />} />
          <Route path="search/:q" element={<SearchVideo />} />
          <Route path="watch/:id" element={<Watchvedio />} />
        </Route>

        <Route path="/homepage">
          <Route index element={<Index />} />
          <Route path="/homepage/:p" element={<Page />}>
            <Route path="companey" element={<Companey />} />
            <Route path="location" element={<Location />} />
            <Route path="video" element={<Video />} />
            <Route path="group" element={<Group />} />
            <Route path="Personal" element={<Personal />} />
            <Route path="request" element={<Request />} />
            <Route path="groupLesson" element={<GroupLesson />} />
            <Route path="personalLesson" element={<PersonalLesson />} />
            <Route path="seminar" element={<Seminar />} />
            <Route path="interview" element={<Interview />} />
            <Route path="review" element={<Review />} />
          </Route>
        </Route>
      </Routes>
    </BrowserRouter>
  );
}

export default App;
