import styled, { css } from "styled-components";

const Exination = ({ total, changePage, nowPage }) => {
  const pageList = [];
  const lastPage = Math.ceil(total / 10);
  const startPage = Math.ceil(nowPage / 10) * 10 - 9;
  const endPage = startPage + 9 > lastPage ? lastPage : startPage + 9;

  for (let i = startPage; i <= endPage; i++) {
    pageList.push(i);
  }

  return (
    <List>
      {nowPage > 1 && (
        <Page
          onClick={() => {
            changePage(nowPage - 1);
          }}
        >
          이전
        </Page>
      )}
      {pageList.map((number) => {
        return (
          <Page
            isActive={nowPage === number}
            onClick={() => changePage(number)}
            key={number}
          >
            {number}
          </Page>
        );
      })}

      {nowPage < lastPage && (
        <Page
          onClick={() => {
            changePage(nowPage + 1);
          }}
        >
          다음
        </Page>
      )}
    </List>
  );
};

const Page = styled.button`
  cursor: pointer;
  ${({ isActive }) =>
    isActive &&
    css`
      background-color: skyblue;
      color: white;
      cursor: not-allowed;
    `}
`;
const List = styled.div`
  display: flex;
  justify-content: center;
`;

export default Exination;
