import styled, { css } from "styled-components";
import { Link, Outlet } from "react-router-dom";
import TopNav from "../organisms/TopNav";
const Main = () => {
  return (
    <>
      <Wrapper>
        <Link to="/naver/movie">
          <BtnMovieLink>
            <PageTitle>Movie</PageTitle>
          </BtnMovieLink>
        </Link>
        <Link to="/naver/book">
          <BtnBookLink>
            <PageTitle isBook>Book</PageTitle>
          </BtnBookLink>
        </Link>
        <Title> Movie or Book</Title>
      </Wrapper>
      <Outlet />
    </>
  );
};

const Wrapper = styled.div`
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  width: 100vw;
  height: 100vh;
`;

const PageTitle = styled.h1`
  color: #9b0200;
  margin: 0;
  font-family: Impact;
  ${({ isBook }) =>
    isBook &&
    `
  color:#EBDDC0`};
`;

const BtnLink = styled.button`
  width: 100vw;
  height: 50vh;
  border: none;
  background: no-repeat;
  background-size: cover;
  padding: 0px;
`;
const BtnMovieLink = styled(BtnLink)`
  background-image: url("assets/movie02.jpg");
`;

const BtnBookLink = styled(BtnLink)`
  background-image: url("assets/book01.jpg");
`;
const Title = styled.h1`
  position: absolute;
  top: 50%;
  margin: 0;
  color: black;
  transform: translateY(-57%);
  font-family: Impact;
  text-shadow: 3px 1px 1px #2b2128;
`;
export default Main;
