import { Link } from "react-router-dom";
import styled from "styled-components";

const BookList = ({ data }) => {
  const handleError = (e) => {
    e.target.src =
      "https://i1.wp.com/makebct.net/wp-content/uploads/2015/01/comingsoon1.gif?fit=800%2C600";
  };

  return (
    <List>
      {data.map(({ link, image, title, isbn }) => {
        return (
          // <a href={link} key={isbn} target="__blank" rel="noreferrer">
          <Link to={`/naver/book/${isbn.split(" ")[1]}`} key={link}>
            <Item>
              <Thumbnail src={image} onError={handleError} />
              <Title dangerouslySetInnerHTML={{ __html: title }} />
            </Item>
          </Link>
          // </a>
        );
      })}
    </List>
  );
};

const List = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  grid-gap: 10px;
  padding: 10px;
`;
const Item = styled.div`
  display: flex;
  flex-direction: column;
`;
const Thumbnail = styled.img`
  width: 100%;
`;
const Title = styled.div``;

export default BookList;
