import { useEffect, useState } from "react";

const Todo = () => {
  const iframe = () => {
    return {
      __html: '<iframe src="./todo/index.html" width="100%" height="100%" />',
    };
  };

  return (
    <div>
      <div
        style={{
          position: "relative",
          width: "100%",
          height: "100%",
          margin: "0px",
          padding: "0px",
        }}
        dangerouslySetInnerHTML={iframe()}
      />
    </div>
  );
};
export default Todo;
