import styled from "styled-components";
import MovieList from "../organisms/MovieList";
import { useEffect, useState } from "react";
import { countryList } from "../data/naver";
import { getMoviList } from "../api/naver";
import { genreList } from "../data/naver";
import Pageination from "../organisms/Pagination";
const Movie = () => {
  const [text, setText] = useState("");
  const [movieList, setMovieList] = useState([]);
  const [country, setCountry] = useState("ALL");
  const [genre, setGenre] = useState();
  const [page, setpage] = useState(1);
  const [total, setTotal] = useState(0);
  const [query, setQuery] = useState("");

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [page]);

  useEffect(() => {
    // IIFE
    searchMovie();
  }, [country, genre, page, query]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setpage(1);
    setQuery(text);
  };

  const searchMovie = async () => {
    if (!query) return;

    // const params = { query: text, country, genre };
    // if (country === "ALL") delete params.country;

    const start = page * 10 - 9;

    const params = { query, start };
    if (country !== "ALL") params.country = country;
    if (genre !== "전체") params.genre = genre;

    const { items, total } = await getMoviList(params);
    setMovieList(items);
    setTotal(total);
  };

  return (
    <Wrapper>
      <PageTitle>Movie</PageTitle>
      <Form onSubmit={handleSubmit}>
        <select onChange={(e) => setCountry(e.target.value)} value={country}>
          <option value="ALL">전체</option>
          {countryList.map(({ name, code }, index) => {
            return (
              <option value={code} key={code}>
                {name}
              </option>
            );
          })}
        </select>
        <InputText
          placeholder="Search"
          onChange={(e) => setText(e.target.value)}
          value={text}
        />
        <BtnSubmit>검색</BtnSubmit>
      </Form>
      <GenreList>
        {genreList.map(({ code, name }, index) => {
          return (
            <GenreItem
              value={code}
              key={code}
              onClick={(e) => {
                return setGenre(e.target.value), setpage(1);
              }}
            >
              {name}
            </GenreItem>
          );
        })}
      </GenreList>

      <MovieList data={movieList} />
      <Pageination
        nowPage={page}
        total={total}
        onPageChange={(page) => setpage(page)}
      />
    </Wrapper>
  );
};

const Wrapper = styled.div``;
const Form = styled.form`
  display: flex;
  padding: 15px;
`;
const PageTitle = styled.h2`
  text-align: center;
  margin-bottom: 0px;
`;
const InputText = styled.input`
  flex: 1;
  margin: 0 10px;
`;
const BtnSubmit = styled.button``;
const GenreList = styled.ul`
  display: flex;
  list-style: none;
  flex-wrap: wrap;
  padding: 0px 15px;
  margin: 0px;
`;
const GenreItem = styled.li`
  cursor: pointer;
  font-size: 10px;
  margin: 5px;
`;
export default Movie;
