import { useEffect, useState } from "react";
import styled from "styled-components";
import axios from "axios";
import BookList from "../organisms/BookList";
import { getBookList } from "../api/naver";
import Exination from "../organisms/ExInation";
import Pageination from "../organisms/Pagination";
import qs from "qs";
import { useLocation, useNavigate } from "react-router-dom";

const Book = () => {
  const [text, setText] = useState("");
  const [data, setData] = useState([]);
  const [total, setTotal] = useState("");
  const [page, setPage] = useState(1);
  const [query, setQuery] = useState("");
  const { search } = useLocation();
  // const pageSearch = useLocation();
  const navigate = useNavigate();
  const onChange = (e) => {
    setText(e.target.value);
  };
  // console.log(page);
  useEffect(() => {
    const result = qs.parse(search.slice(1));
    const { page, query } = result;

    if (query) {
      setQuery(query);
      setText(query);
      setPage(+page);
    } else {
      reset();
    }
  }, [search]);

  const reset = () => {
    setText("");
    setQuery("");
    setPage(1);
    setTotal(0);
    setData([]);
  };

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [page]);

  const searchBoox = async () => {
    if (!query) return;
    const startPage = page * 10 - 9;
    const { items, total } = await getBookList({
      query,
      start: startPage,
    });
    setData(items);
    setTotal(total);

    const search = qs.stringify({ query, page });

    // navigate({ search: search });

    navigate({ search: search });
  };

  useEffect(() => {
    searchBoox();
  }, [page, query]);

  const onSubmit = async (e) => {
    e.preventDefault();
    setQuery(text);
    setPage(1);
  };

  return (
    <Container>
      <PageTitle>Book</PageTitle>
      <Form onSubmit={onSubmit}>
        <Input onChange={onChange} value={text} />
        <BtnSubmit>검색</BtnSubmit>
      </Form>
      <BookList data={data} />
      {/* <Exination
        total={total}
        nowPage={page}
        changePage={(page) => setPage(page)}
      /> */}
      <Pageination
        total={total}
        nowPage={page}
        onPageChange={(page) => setPage(page)}
      />
    </Container>
  );
};

const Container = styled.div``;
const PageTitle = styled.h2`
  text-align: center;
  margin-bottom: 0px;
`;
const Form = styled.form`
  display: flex;
  padding: 15px;
`;
const Input = styled.input`
  flex: 1;
`;
const BtnSubmit = styled.button``;

export default Book;
