import axios from "axios";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { getBookDetail } from "../api/naver";
import styled from "styled-components";

const BookDetail = () => {
  const [book, setBook] = useState({});

  const params = useParams();

  const { isbn } = params;

  useEffect(() => {
    searchBook();
  }, []);

  const searchBook = async () => {
    const result = await axios.get("/v1/search/book_adv.json", {
      headers: {
        "X-Naver-Client-Id": "XRupn7GoMOzhZZQE_RD6",
        "X-Naver-Client-Secret": "FxfJq0rU7f",
      },
      params: {
        d_isbn: isbn,
      },
    });
    setBook(result.data.items[0]);
    console.log(result.data);
  };

  return (
    <>
      <Img src={book.image} />
      <Title>{book.title}</Title>
      <Text>
        가격:{book.discount} 출판사 {book.publisher}
      </Text>
    </>
  );
};

const Img = styled.img`
  display: block;
  margin: 10px auto;
  width: 50vw;
`;
const Title = styled.h2`
  padding: 0px 10px;
`;
const Text = styled.h3`
  text-align: center;
`;

export default BookDetail;
